@import url(https://fonts.googleapis.com/css?family=Abel|Anton|Bai+Jamjuree|Indie+Flower|Mali|Niramit|Open+Sans|Roboto|Shadows+Into+Light);
@import url(https://use.fontawesome.com/releases/v5.3.1/css/all.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.secSocials ul li{    
    display: inline-block;
    margin-right: 10px;
}

.secSocials ul li a:link {
    color: #fff;    
    text-decoration: none;
}
.secHeader {    
    background-color: #222;
    padding: 20px 10px 10px 10px;
    color: white;
    text-align: center;
}

.secHeader h1 {
    font-weight: 700;
    font-size: 1.6em;
    margin: 10px 0px 0px 0px;
    text-transform: uppercase;    
}

.secHeader h2 {
    font-family: sans-serif;
    font-size: 1.2em;
    font-weight: 200px;
    margin: 0px 0px 10px 0px;
}

.secHeader .desc {
    font-family: 'Bai Jamjuree', sans-serif;
    margin: 0 auto;
    width: 80%;
}

.secHeader .divHeaderImgProfile {
    height: 200px;
}

.secHeader .divHeaderImgProfile img{
    border-radius: 200px;
    -moz-border-radius: 200px 200px 200px 200px;
    -webkit-border-radius: 200px 200px 200px 200px;
    border: 0px solid #fff;
    height: 100%;
}

@media (min-width: 515px) {
    .secHeader h1 {
        font-size: 1.7em;
    }
    
    .secHeader h2 {
        font-size: 1.3em;
    }
}

@media (min-width: 677px) {
    .secHeader .desc {
        width: 70%;
    }
}

@media (min-width: 677px) {
    .secHeader .desc {
        width: 60%;
    }
}

@media (min-width: 1100px) {
    .secHeader .desc {
        width: 45%;
    }
}


.bboard{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.bbulletin{
    border-radius: 30px;
    border-style: solid;
    border-width: 2px;
    margin: 1em;
    text-align: center;
    width: 80%;
}

.bbulletin .title{
    margin-bottom: 0.2em;
    margin-top: 0.5em;
}

.bbulletin .attributes{
    padding: 0em 0em 0em 4em;
}

.bbulletin .attributes li{
    font-size: 0.8em;
    list-style: none;
    text-align: left;
    text-decoration: none;
    /* text-transform: uppercase; */
}

.bbulletinAnimation{    
    -webkit-animation-duration: 5s;    
            animation-duration: 5s;
    -webkit-animation-name: zoonin;
            animation-name: zoonin;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

.level{
    margin-top: 0px;
}

@-webkit-keyframes zoonin {
    from {
        -webkit-transform:scale(0.30);
                transform:scale(0.30);
        /* margin-right: 500%; */
        /* width: 300% */
    }
  
    to {
        -webkit-transform:scale(1);
                transform:scale(1);
        /* margin-right: 0%; */
        /* width: 100%; */
    }
}

@keyframes zoonin {
    from {
        -webkit-transform:scale(0.30);
                transform:scale(0.30);
        /* margin-right: 500%; */
        /* width: 300% */
    }
  
    to {
        -webkit-transform:scale(1);
                transform:scale(1);
        /* margin-right: 0%; */
        /* width: 100%; */
    }
}


@media (min-width: 601px) {
    .bbulletin{
        /* padding: 1em; */
        margin: 2em;
        text-align: center;
        width: 38%;
    }
}

@media (min-width: 1051px) {
    .bbulletin{
        /* padding: 1em; */
        margin: 2em 3em;
        text-align: center;
        width: 35%;
    }
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

